<template>
  <v-app>
    <!-- 应用栏 -->
    <v-app-bar app dense color="white">
      <div class="d-flex" style="align-items: center; margin: 0 auto; width: 1185px">
        <v-toolbar-title class="text-h6 font-weight-black px-3">弹头奇兵工具箱</v-toolbar-title>

        <v-btn text v-for="(item, index) in items" :key="index" :to="item.to" plain>
          {{ item.title }}
        </v-btn>
      </div>
    </v-app-bar>

    <v-main style="margin: 0 auto; width: 1185px">
      <!-- 内容 -->
      <v-container class="py-5" fluid @scroll.passive="onScroll">
        <router-view />
      </v-container>

      <!-- 页脚 -->
      <!--    <v-footer
        padless
        color="transparent"
        class="d-flex justify-center py-2 text--secondary text-button">
        <v-icon small>mdi-copyright</v-icon>
        2025
      </v-footer>-->
    </v-main>

    <!-- 返回顶部 -->
    <v-hover v-slot="{ hover }" open-delay="200">
      <v-btn
        height="42"
        width="42"
        color="black"
        x-small
        bottom
        right
        fixed
        :elevation="hover ? 6 : 2"
        @click="goTop">
        <v-icon :color="hover ? 'yellow' : 'white'">mdi-chevron-up</v-icon>
      </v-btn>
    </v-hover>
  </v-app>
</template>

<script>
export default {
  name: 'Layout',

  data() {
    return {
      items: [
        { title: '首页', to: '/' },
        { title: '配装', to: '/simulator' },
        { title: '武器', to: '/weapon' },
        { title: '防具', to: '/armor' },
      ],
      drawer: null,
      mini: false,
    };
  },

  mounted() {},

  methods: {
    goTop(e) {
      window.scroll({
        top: 0,
        left: 0,
        behavior: 'smooth', // 平滑滚动
      });
    },
  },
};
</script>
